.hero-img {
  width: 100%;
  background: rgba(0, 0, 0, 0.5);
  height: 75vh;
  position: relative;
}
.hero-img::before {
  content: "";
  background-image: url("https://images.unsplash.com/photo-1537432376769-00f5c2f4c8d2?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1325&q=80");
  background-size: cover;
  background-position: center top;
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
}

.heading {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.hero-img h1{
    font-size: 2.4rem;
}

.hero-img p{
    font-size: 1.4rem;
}

@media screen and  (max-width: 640px) {
    .hero-img h1{
        font-size: 2rem;
    }
}
